@import url(https://fonts.googleapis.com/css2?family=Inconsolata&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Inconsolata&display=swap);
body{
  margin: 0px;
  background-color: rgb(41, 39, 39);
}
.App {
  margin-top: 50px;
  text-align: center;
}
.sub-nav{
  display: flex;
  width: 100%;
  justify-content: center;
}
.sub-sub-nav{
  width: 50%;

}

.row {
  display: flex;
  justify-content: center;
}

.alert{
  background-color: black;
  border: yellow solid 2px;
 
}
.text-alert{
  color: yellow;
}
.box{
  display: flex;
  position: fixed;
  top:0;
  width: 100%;
  height: auto;
  padding: 5px;
  padding-left: 11px;
  padding-bottom: 11px;
  justify-content: space-between;
  background-color: black;
  border-bottom: yellow solid 2px;
  z-index: 2;
}
.nav-rigth{
  padding-right: 10px;
}
.logo{
  margin-top: 8px;
  width: 150px;
  height: auto;
}
.nav-left{
  display: flex;
  flex-direction: row;
  color: white;
}
.span-text{
  font-family: 'Inconsolata', monospace;
  color:yellow;
  padding-top: 5px;
  padding-right: 4px;
  font-size: 24px;
}
.card {
  margin: 20px;
  width: 18rem;
  color:black;
  background: rgb(0,0,0);
  background: linear-gradient(177deg, rgba(0,0,0,1) 0%, rgba(255,255,255,1) 35%, rgba(244,250,5,1) 100%);
  border-radius: 0px 0px 12px 12px;
}

.card-title {
  margin-bottom: 15px;
}

.close-icon {
  display: flex;
  justify-content: flex-end;
  margin-right: 8px;
  margin-top: 8px;
}

.card-body {
  margin-top: 0px !important;
  padding-top: 0px !important;
}
.min{
  padding: 0px;
  margin: 0px;
 
  font-weight: 800;
}
.max{
  padding: 0px;
  margin: 0px;
 
  font-weight: 800;
}
.num-min{
  padding: 0px;
  margin: 0px;
  font-size: 32px;
  font-weight: 800;
}
.num-max{
  padding: 0px;
  margin: 0px;
  font-size: 32px;
  font-weight: 800;
}
.min-arrow{
  color: blue;
}
.max-arrow{
  color: red;
}
.btn-full-data{
  background-color: black;
  color: yellow;
  margin-right: 15px;
  margin-left: 5px;
  padding: 4px;
  border-radius: 0px 0px 8px 8px;
}
.btn-full-data:hover{
  background-color: rgb(218, 211, 15);
  color: rgb(7, 7, 7);
}
.text-full-data{
  color: yellow;
}
.title-full-data{
  color: yellow;
}
.cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.non-cities{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 185px ;
  height: 100px;
  background-color: rgb(225, 211, 16);
  color: black;
  font-size: 24px;
  margin-left: 16%;
  margin-right: 16%;
  border-radius: 5px 5px 5px 5px;
}
.footer{
  display: flex;
  position: fixed;
  bottom:0;
  width: 100%;
  height: auto;
  padding: 5px;
  justify-content: center;
  align-items: center;
  background-color: black;
  border-top: yellow solid 2px;
  z-index: 2;
}


.text-footer{
  display: flex;
  padding-top: 5px;
  font-family: 'Inconsolata', monospace;
  color: white;
  align-self: center;
}
.btn-search{
    background-color: black;
    color: yellow;
    margin-right: 15px;
    margin-left: 5px;
    padding: 7px;
    border-radius: 0px 0px 12px 12px;
    outline: none;
    font-weight: 800;
}
.btn-search:hover{
    background-color: yellow;
    color: black;

}

input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:focus, 
input:-webkit-autofill:active  {
    -webkit-box-shadow: 0 0 0 30px yellow inset !important;
    
    
}

.btn-search:focus,
.btn-search:active{
    background-color: black;
    color: yellow;
    outline: none;
}
.search-section{
    margin-top: 5px;
}
.inp-search{
    /*background-color: rgb(46, 46, 46);*/
    background-color: yellow;
    color: black;
    font-weight: 800;
    padding: 5px;
    width: 70%;
    border-radius: 0px 0px 12px 12px;
    outline: none;
}
@media (max-width: 767.98px) { 
    .inp-search{
        /*background-color: rgb(46, 46, 46);*/
        background-color: yellow;
        color: black;
        font-weight: 800;
        padding: 5px;
        width: 100%;
        border-radius: 0px 0px 12px 12px;
        outline: none;
    }
 }
